/* Breakpoints */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

html {
  height: 100%;
  width: 100%; }

body {
  height: 100%;
  width: 100%; }

#root {
  height: 100%;
  width: 100%; }

.svg-sprite {
  width: 0;
  height: 0;
  position: absolute;
  visibility: hidden; }

.app {
  background: #F4F7FF;
  height: 100%; }

.tab {
  margin: 0 1rem;
  cursor: pointer;
  font-weight: 500;
  color: #555; }
  .tab:after {
    content: '';
    border-bottom: 2px solid #fff;
    width: 30%;
    height: 10px;
    display: block; }
  .tab:hover {
    color: #2864FF; }

.tab-weekly li:first-child:after {
  content: '';
  border-bottom: 2px solid #2864FF;
  width: 30%;
  height: 10px;
  display: block; }

.tab-fornightly li:nth-child(2):after {
  content: '';
  border-bottom: 2px solid #2864FF;
  width: 30%;
  height: 10px;
  display: block; }

.tab-monthly li:last-child:after {
  content: '';
  border-bottom: 2px solid #2864FF;
  width: 30%;
  height: 10px;
  display: block; }

.header {
  background: #fff;
  display: flex;
  justify-content: space-between;
  padding: 1.125rem 1rem; }
  @media (min-width: 48rem) {
    .header {
      padding: 1.125rem 3rem; } }
  .header__logo {
    width: 9rem; }
  .header__total-price {
    display: flex;
    align-items: center; }
    .header__total-price svg {
      width: 1.5rem;
      height: auto;
      margin-right: .75rem; }
    .header__total-price span {
      font-size: 1.125rem;
      font-weight: 600; }

.container {
  max-width: 50rem;
  margin: auto;
  margin-top: 3rem;
  padding: 1rem; }

.plan-card {
  background: #fff;
  padding: 1.5rem;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .plan-card h1 {
    font-size: 1.75rem;
    margin: 0;
    color: #3b475c;
    text-transform: uppercase;
    letter-spacing: 3px; }
  .plan-card ul {
    display: flex;
    list-style: none;
    padding: 0; }
  .plan-card__plan-wrapper {
    width: 100%;
    margin-bottom: .25rem; }
    .plan-card__plan-wrapper--plan {
      display: flex;
      border: 1px solid #B0B0B0;
      cursor: pointer;
      justify-content: center;
      margin-bottom: .75rem; }
      .plan-card__plan-wrapper--plan p {
        margin-right: 1rem;
        color: gray; }
    .plan-card__plan-wrapper--plan:hover {
      border: 1px solid #2864FF; }
    .plan-card__plan-wrapper--plan-selected {
      border: 1px solid #2864FF; }
    .plan-card__plan-wrapper--plan-unselected {
      opacity: .4; }

.button {
  font-size: 1.125rem;
  font-weight: 500;
  cursor: pointer; }
  .button--back {
    color: #555; }
    .button--back:hover {
      text-decoration: underline; }
    .button--back::before {
      content: '';
      border-width: 1rem;
      border-top: 2px solid #555;
      border-left: 2px solid #555;
      -webkit-transform: rotate(-54deg) skew(-20deg);
              transform: rotate(-54deg) skew(-20deg);
      overflow: hidden;
      display: inline-block;
      font-size: 1px;
      width: .5rem;
      height: .5rem;
      margin-right: .25rem; }
  .button--submit {
    background: #3d73ff;
    border: none;
    color: #fff;
    padding: .75rem 1rem;
    align-self: flex-end;
    font-weight: 400;
    font-size: 1rem; }

